h1, h2, h3, p {
  font-family: 'Prata', serif;
  font-weight: unset;
}

h1 {
  color: #229B67;
}

a:focus {
  outline: 2px solid #229B67;
}

.centered {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
}

h1 {
    font-size: 64px;
}

